.title{
    font-size: 2.5em;
    text-align: center;
    margin: 0;
    padding: 48px;
  }
.luibutonDiv{
    width: 55%;
    display: flex;
    padding-inline-start: 40px;
    justify-content: flex-end;
    padding-inline-end: 1em;
    max-width: 806.422px;
}
.luibuton{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: none;
    background-color: rgb(27, 27, 27);
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    transition-duration: .3s;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.11);
    font-size: 45px;
    font-weight: bold;
    color: whitesmoke;
}
.luibuton:hover {
    background-color: rgb(150, 94, 255);
    transition-duration: .3s;
    transform: rotate(0.4turn);
  }
.input {
    color: #8707ff;
    border: 2px solid #8707ff;
    border-radius: 10px;
    padding: 10px 25px;
    background: transparent;
    max-width: 458px;
    width: 37%;
    font-size: 16px;
    }
   
   .input:active {
    box-shadow: 2px 2px 15px #8707ff inset;
   }