ul{
    width: 55%;
    gap: 10px;
    display: grid;
    max-width: 814.422px;
    margin-top: 2%;
}
li{
    list-style: none;
    background-color: #232228;
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    padding-inline: 3%;
    min-width: 252.812px;
}
 .check{
    margin: 1em 0 1em 0;
 }
  
svg{
    margin-block: auto;
    cursor: pointer;
    font-size: 23px;
}
svg:hover{
    color: red;
    transition-duration: .5s;
}
.tacho{
    text-decoration:line-through;
    
}
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
   }
   
   .container {
    display: block;
    position: relative;
    cursor: pointer;
    font-size: 20px;
    user-select: none;
    padding-top: 2%;
   }
   
   /* Create a custom checkbox */
   .checkmark {
    position: relative;
    top: 0;
    left: 0;
    height: 1.3em;
    width: 1.3em;
    background: #606062;
    border-radius: 7px;
   }
   
   /* When the checkbox is checked, add a blue background */
   .container input:checked ~ .checkmark {
    background-image: linear-gradient(#255CD2,#1D52C1)
   }
   
   /* Create the checkmark/indicator (hidden when not checked) */
   .checkmark:after {
    content: "";
    position: absolute;
    display: none;
   }
   
   /* Show the checkmark when checked */
   .container input:checked ~ .checkmark:after {
    display: block;
   }
   
   /* Style the checkmark/indicator */
   .container .checkmark:after {
    left: 0.45em;
    top: 0.20em;
    width: 0.24em;
    height: 0.6em;
    border: solid white;
    border-width: 0 0.15em 0.15em 0;
    transform: rotate(45deg);
   }