.ModalBackground {
    background-color: rgba(32,35,41,.8);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

.TodoForm {
    margin-bottom: 20px;
    width: 40%;
    background: #393e4670;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    border: 1px solid #8707ff;
  }
  label{
    font-size: 24px;
  }
 
  textarea {
    width: 82%;
    height: 100px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
    margin-top: 12px;
  }
  
  .TodoForm-CreateButtonDiv{
    display: flex;
    justify-content: center;
    margin: 24px;
  }
  button {
    background-color: #4FC0D0;
    padding: 8px 16px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    color: white;
    cursor: pointer;
  }
  
  button[type='button'] {
    background-color: #f44336;
  }
  
  button:hover {
    opacity: 0.8;
  }

  .TodoForm-CancelButtonDiv{
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  .TodoForm-CancelButtonDiv label{
    margin: 8px 8px 0 0;
    height: 23px;
  }